<template>
  <div class="sureMailBody">
    <div class="user-header">
      <a class="nav-item1" @click="sureMailBodyBack"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
      <a class="ordertails">确认邮寄</a>
    </div>
    <div class="sureMailMain">
      <div class="addxinxi" v-show="addShow" @click="addressBtn">
        <img src="../../assets/images/thebar/wh_47.png"/>
        <a>添加地址信息</a>
      </div>
      <div class="management" id="management" v-show="addressShow">
        <div class="consignee"><a>收货人：<span>{{name}}</span></a><a>{{iphone}}</a></div>
        <div class="shipping"><a>收货地址：{{province + city + district + address}}<span></span></a></div>
        <div class="defaultAddress">
          <p class="modify" @click="changaddress">更改收货人/地址<img src="../../assets/images/thebar/bc_25.png"/></p>
        </div>
      </div>
      <p v-show="dollhistory" class="wudoll">暂无娃娃订单!</p>
      <div class="divclass">
        <div class="mailOrder" v-for="i in myDoll">
          <div class="orderHead">
            <a>申请时间:{{i.created_at}}</a>
          </div>
          <div class="orderBody">
            <img :src="i.icon_url"/>
            <span>
              <a>{{i.name}}</a>
              <a>×{{i.num}}</a>
            </span>
          </div>
          <div class="orderFoot">
            <a>共{{i.num}}件</a>
          </div>
        </div>
      </div>

    </div>
    <div class="submitOrders">
      <div class="packageMail"><a>满3只包邮</a></div>
      <div class="submit" @click="submitBtn">提交</div>
    </div>
    <div class="alert-charge" v-show="charge">
      <transition name="modal">
        <div class="charge-mask">
          <div class="modal-header">
            <p><img @click="chargeDele" src="../../assets/images/room/gc_16.png"/><a>确认订单</a></p>
          </div>
          <div class="modal-body">
            <div class="charge-t"><span>商品名：<a>{{shoping}}</a></span></div>
            <div class="charge-t"><span>数&nbsp;&nbsp;&nbsp;量：<a>{{num}}</a></span></div>
            <div class="charge-t"><span>金&nbsp;&nbsp;&nbsp;额：<a>{{money}}元</a></span></div>
            <!--<div class="charge-t"><span>折扣价：<a>{{discount}}元</a></span></div>-->
            <!--<ul class="charge-m">-->
            <!--<li><a>可立即获得&nbsp;<span>480</span>&nbsp;钻石</a></li>-->
            <!--<li><a>有效期内每天获得&nbsp;<span>35</span>&nbsp;钻石,7天共赠送&nbsp;<span>245</span>&nbsp;钻石(需要每天登陆领取才可获得)</a></li>-->
            <!--</ul>-->
            <div class="charge-way">
              <ul>
                <li @click="weixin" v-show="weixinShow">
                  <p class="zhifubao-l"><img src="../../assets/images/room/bc_38.png"/><a>微信支付</a></p>
                  <p class="zhifubao-r" v-show="show"><img src="../../assets/images/room/bc_42.png"/></p>
                </li>
                <!--<li @click="other">-->
                  <!--<p class="zhifubao-l"><img src="../../assets/images/room/bc_40.png"/><a>其他支付</a></p>-->
                  <!--<p class="zhifubao-r" v-show="otherShow"><img src="../../assets/images/room/bc_42.png"/></p>-->
                <!--</li>-->
              </ul>
            </div>
          </div>
          <div @click="paySure" class="modal-footer">
            <a>确认支付</a>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
  var urlhost = process.env.API_AJAX;
  export default {
    data() {
      return {
        dollStr:'',
        myDoll:[],
        account:'',
        mobile:'',
        name:"",
        iphone:'',
        wechat_openid:"",
        addShow:false,
        addressShow:false,
        province:'',
        city:'',
        district:'',
        address:'',
        address_id:'',
        charge:false,
        show:false,
        weixinShow:true,
        otherShow:false,
        discount:'',
        shoping:'',
        num:'',
        money:'',
        index:'',
        dollhistory:false,

      }
    },
    components: {
    },
    created:function () {
      var _this = this;
      var user_id = getCookie('userId');
        $.ajax({
        type: 'GET',
        url: urlhost+"useraddress/defaddress?user_id="+user_id,
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          var status = JSON.parse(response).status;
          var mess = JSON.parse(response).message;
          if(status == 0){
          _this.addShow = true;
          _this.addressShow = false;
        }else{
          _this.addShow = false;
          _this.addressShow = true;
          _this.name = mess.consignee;
          _this.province = mess.province;
          _this.city = mess.city;
          _this.district = mess.district;
          _this.address = mess.address;
          _this.iphone = mess.mobile;
          _this.address_id = mess.id;
        }
        },
      });

    },
    mounted:function () {
      var format = function (timestamp) {
        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '/';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '/';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() < 10 ? '0'+(date.getMinutes()) : date.getMinutes();
        return Y+M+D+h+m;
      }
      var _this = this;
      var ua = window.navigator.userAgent.toLowerCase();
      if(ua.match(/MicroMessenger/i) == 'micromessenger'){
        _this.index = 0//微信支付
      }else{
        _this.index = 1//其他支付
      }
      var user_id = getCookie('userId');
      $.ajax({
        type: 'GET',
        url: urlhost+"usercenter/getphonemiamond?user_id="+user_id,
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          var mess = JSON.parse(response).message;
        _this.account = mess.account;
          if(mess.mobile == null){
            _this.mobile = "";
          }else{
            _this.mobile = mess.mobile;
          }
        _this.wechat_openid = mess.wechat_openid;
        },
      });
      var doll_id = _this.$route.query.id;
      _this.dollId  = doll_id.toString();
//      娃娃邮寄接口
      $.ajax({
        type: 'GET',
        url: urlhost+"usercenter/playpack?pack_id="+_this.dollId,
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          var mydoll = JSON.parse(response).message;
          var status = JSON.parse(response).status;
          if(status == 0){
            _this.dollhistory = true;
          }else{
            for(var i=0;i< mydoll.length;i++){
              var num = parseInt(mydoll[i].created_at)
              var time = format(num);
              mydoll[i].created_at = time;
            }
            _this.myDoll = mydoll;
          }
        },
      });

    },
    methods:{
      chargeDele:function () {
        this.charge = false;
      },
      weixin:function () {
        this.show = true;
        this.otherShow = false;
        this.index = 0;

      },
      other:function () {
        this.show = false;
        this.otherShow = true;
        this.index = 1;
      },
      paySure:function () {
        var _this = this;
        var userId  = getCookie("userId");
        var token = getCookie("wawajitoken");
        var num = _this.myDoll.length;
        var notify = "http://weixin.fkzzz.cn/admin/expressorder";
        var wechatnotify = "http://weixin.fkzzz.cn/wechatpay/expressorder";
        var nreturn = encodeURIComponent("https://fkzzz.cn/#/usercenter/payresults");
        var index = _this.index;
        switch (index){
          case 0://微信支付
            window.location.href = "http://weixin.fkzzz.cn/wechatpay"+"?"
              + "user_id="+userId+"&"
              +"user_name="+_this.account+"&"
              +"user_phone="+_this.mobile+"&"
              + "goods_name="+"快递费"+"&"
              +"num="+_this.num+"&"
              +"amount="+_this.money+"&"
              +"discount="+_this.discount*_this.money+"&"
              +"notify="+wechatnotify+"&"
              +"pack_id="+_this.dollStr+"&"
              +"type="+2+"&"
              +"address_id="+_this.address_id+"&"
              +"wechat_openid="+_this.wechat_openid+"&"
              +"token="+token;
            break;
          case 1://其他支付
            window.location.href = "http://weixin.fkzzz.cn/usercenter/playpackorder"+"?"
              + "user_id="+userId+"&"
              +"user_name="+_this.account+"&"
              +"user_phone="+_this.mobile+"&"
              + "goods_name="+"快递费"+"&"
              +"num="+num+"&"
              +"amount="+15+"&"
              +"discount="+15+"&"
              +"notify="+notify+"&"
              +"nreturn="+nreturn+"&"
              +"pack_id="+_this.dollStr+"&"
              +"address_id="+_this.address_id+"&"
              +"wechat_openid="+_this.open_id+"&"
              +"token="+token;
            break;
        }
      },
      sureMailBodyBack:function () {
        this.$router.go(-1);
      },
      addressBtn:function () {
        this.$router.push({path:"/usercenter/newAddress"})
      },
      changaddress:function () {
        var _this = this;
        _this.$router.push({
          path:"/usercenter/changAddress",
          query:{
            id:_this.address_id,
          }
        })
      },
      submitBtn:function () {
        var _this = this;
        if(_this.addressShow == true){
          var dollId = [];
          var userId  = getCookie("userId");
          var dollLen = _this.myDoll.length;
          _this.myDoll.forEach(function (i) {
            dollId.push(i.id)
          })
          _this.dollStr = dollId.toString();
          if(dollLen >= 3){
            $.ajax({
              type: 'POST',
              url: urlhost+"usercenter/packorder",
              data$Type: "jsonp",
              data: {
                'user_id': userId,
                'pack_id':_this.dollStr,
                "address_id":_this.address_id
              },
              success: function (response) {
                var msg = JSON.parse(response);
                if(msg.status === 0){
                  _this.$layer.msg(msg.message)
                }else if(msg.status === 1){
                  _this.$layer.msg("提交成功，发货中...");
                  window.history.go(-1);
                }
              },
            });
          }else if(dollLen == 0){
            _this.$layer.msg("暂无提交订单!");
          }else{
            _this.charge = true;
            _this.shoping = "快递费";
            _this.num = dollLen;
            _this.money = 15;
            _this.discount = 1;
          }
        }else{
          _this.$alert("有地址才能邮寄哦亲!");
        }

        }
      }
  }
</script>
<style lang="scss" scoped>
  /*确认邮寄界面*/
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .sureMailBody{
    position: fixed;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    top: 0px;
    background-color: #f2f2f2;
  .user-header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: n(80);
    background: #fff001;
    display: flex;
    line-height: n(80);
    z-index: 999;
    border-bottom: 1px solid #d8d8d8;
  .nav-item1{
    position: absolute;
    left: 0.2rem;
  .game_1{
    width: n(50);
    height: n(50);
  }
  }
  .ordertails{
    font-size: 14px;
    font-weight: bold;
    width: 100%;
    display: inline-block;
    text-align: center;
  }
  }
  .sureMailMain{
    width: 100%;
    margin-top: n(80);
    .wudoll{
      padding: n(20);
      color: red;
    }
  .addxinxi{
    width: 100%;
    height: n(175);
    background: #ffffff;
    display: inline-flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  img{
    width: n(80);
    height: n(80);
  }
  }
    .management{
      margin-top: n(20);
      padding: 10px;
      width: 100%;
      background: #ffffff;
      .consignee{
        display: inline-flex;
        justify-content: space-between;
        width: 100%;
        padding-bottom: 8px;
      }
      .shipping{
        padding-bottom: 8px;
        word-break: break-all;
        word-wrap: break-word;
        border-bottom: 1px solid #f2f2f2;
      }
      .defaultAddress{
        width: 100%;
        height: n(50);
        display: inline-flex;
        align-items: center;
        .modify{
          width: 100%;
          text-align: right;
          img{
            width: n(40);
            height: n(40);
          }
        }
      }
    }
 .divclass{
   width: 100%;
   .mailOrder{
     margin-top: 10px;
     background: #ffffff;
     padding-left: 10px;
     padding-right: 10px;
     .orderHead{
       width: 100%;
       display: inline-flex;
       align-items: center;
       height: n(50);
       border-bottom: 1px solid #f2f2f2;

     }
     .orderBody{
       height: n(100);
       border-bottom: 1px solid #f2f2f2;
       display: inline-flex;
       align-items: center;
       width: 100%;
       img{
         float: left;
         display: block;
         width: n(85);
         height: n(85);
         -webkit-border-radius: 5px;
         -moz-border-radius: 5px;
         border-radius: 5px;

       }
       span{
         margin-left: 13px;
         float: left;
         height: n(85);
         display: inline-flex;
         justify-content: center;
         flex-direction: column;
       }
     }
     .orderFoot{
       width: 100%;
       height: n(50);
       display: inline-flex;
       align-items: center;
       justify-content: space-between;
       a{
         img{
           width: n(40);
           height: n(40);
         }
       }

     }
   }
 }
  }
  .submitOrders{
    width: 100%;
    position: fixed;
    bottom: 0;
    height:n(77) ;
    background: #ffffff;
  .packageMail{
    float: left;
    width: 70%;
    height: n(77);
    line-height: n(77);
    text-align: right;
  a{
    margin-right: 10px;
  }
  }
  .submit{
    float: left;
    width: 30%;
    height: n(77);
    text-align: center;
    line-height: n(77);
    background: #fef102;
  }
  }
    .alert-charge{
      position: fixed;
      z-index: 9998;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, .5);
      display: table;
      transition: opacity .3s ease;
    }

    .charge-mask{
      position: relative;
      width: 80%;
      height: n(767);
      background: #f4f4f4;
      margin-left: 10%;
      margin-top: 20%;
      -webkit-border-radius: 15px;
      -moz-border-radius: 15px;
      border-radius: 15px;
      overflow: hidden;
      .modal-header{

        width: 100%;
        height: n(70);
        background: #fff001;
        p{
          width: 100%;
          height: 100%;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          position: relative;
          img{
            position: absolute;
            left: 10px;
            width: n(50);
            height: n(50);
          }
          a{
            font-size: 14px;
          }
        }
      }
      .modal-body{
        width: 80%;
        margin-left: 10%;
        margin-top: n(40);
        .charge-t{
          display: inline-flex;
          align-items: center;
          margin-left: n(50);
          width: 100%;
          height: n(50);
          img{
            width: n(50);
            height: n(50);
          }
          span{
            margin-left: n(20);
            a{
              font-weight: bold;
            }
          }
        }
        .charge-m{
          width: 80%;
          margin-left: 10%;
          list-style-type:disc;
          li{
            margin-top: 10px;
            color: #007add;
            a{
              letter-spacing: 1px;
              span{
                color: #017ae2;
              }
            }
          }
        }
        .charge-way{
          margin-top: 20px;
          width: 80%;
          margin-left: 10%;
          ul{
            li{
              width: 100%;
              height: n(60);
              display: inline-flex;
              align-items: center;
              justify-content: space-between;
              .zhifubao-l{
                height: n(44);
                img{
                  width: n(44);
                  height: n(44);
                }
                a{
                  margin-left: 5px;
                }
              }
              .zhifubao-r{
                height: n(44);
                img{
                  width: n(27);
                  height: n(21);
                }
              }
            }
          }
        }
      }
      .modal-footer{
        position: absolute;
        bottom:0;
        width: 100%;
        height: n(80);
        background: #fff001;
        text-align: center;
        line-height: n(80);
        a{
          font-size: 14px;
        }
      }
    }
  }
</style>



